.roomLink {
  font-size: 22px;
  color: #1678c2;
  text-decoration: underline;
}

body {
  padding: 10px 5px 0;
  font-size: 16px;
}

#root {
  height: 100%;
}

.resistance.text {
  color: #55a5b0!important;
}

.spy.text {
  color: #c40b15!important;
}

.success.text {
  color: #21ba45;
}

.danger.text {
  color: #db2828;
}

ul {
  margin: 5px 0;
}
.room {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}


.room-content {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    overflow-y: auto;
    padding: 3px;
}

.room-menu {
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
}

.room-msg {
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
}
